@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';

.eureka-list-item__text-container {
  padding: 1.2rem 0;
}

.kanban-task__task-row1, .kanban-task__task-row2 {
  padding-bottom: 7px;
  @include eureka-flex-row;
}

.kanban-task__task-row2 {
  .kanban-task__task_tags {
    flex: 1 1 auto;
  }

  .eureka-button__content-container {
    padding: 0px;
  }
  .eureka-button__icon-container {
    padding: 0;
  }
}

.kanban-task__task-row1 {

  .eureka-avatar {
    flex: 0 0 30px;
    max-height: 30px;
    max-width: 30px;
    height: 30px;
    width: 30px;
  }

  .kanban-task__task-text {
    flex: 1 1 auto;
  }
}